<template>
	<view class="page-container withdrawal-container">
		<uni-nav-bar dark :fixed="true" status-bar left-icon="left" :border="false" title="提现" @clickLeft="goBack" />
		<view class="p-3">
			<uni-forms ref="withdrawalForm" :modelValue="withdrawalForm" label-width="0">
				<view class="withdrawalForm">
					<uni-forms-item label=" ">
						<uni-data-select
							v-model="withdrawalForm.type"
							:localdata="typeOptions"
							@change="typeChange"
							placeholder="提现方式"
						></uni-data-select>
					</uni-forms-item>
					<uni-forms-item label=" ">
						<uni-data-select
							v-model="withdrawalForm.account"
							:localdata="accountOptions"
							placeholder="账号"
						></uni-data-select>
					</uni-forms-item>
					<uni-forms-item label=" ">
						<uni-easyinput v-model="withdrawalForm.amount" placeholder="提现金额" />
						<!-- 可提现金额 -->
						<view class="mt-2 d-flex justify-content-between fs_12">
							<view>
								<text class="text-grey-150">可提金额：</text>
								<text class="text-grey-150">￥{{ allAmount }}</text>
								<text class="ml-3 text-primary" @click="allWithdrawal">全部提现</text>
							</view>
							<text class="text-grey-200" @click="goDetail()">提现记录</text>
						</view>
					</uni-forms-item>
				</view>
			</uni-forms>
			<view class="p-btn">
				<view class="my-btn bg-primary-600 p-2 fs_14" type="primary" size="mini" @click="apply()">确认提现</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				withdrawalForm:{
					type:"",
					account:"",
					amount:"",
				},
				typeOptions: [
					{ value: 1, text: "支付宝" },
					{ value: 2, text: "银行卡" },
				],
				accountOptions: [],
				allAmount: "",
			}
		},
		onLoad(){
			this.getBalance();
		},
		methods: {
			goBack() {
				uni.navigateBack({
					delta: 1
				})
			},
			
			// 获取余额
			getBalance(){
				this.$api.balanceInfo({
					token:uni.getStorageSync('user_token')
				}).then(res=>{
					if(res.code == 0){
						this.allAmount = res.data.balance;
					}else{
						uni.showToast({
							title: res.msg,
							icon: 'none'
						});
					}
				});
			},
			
			// 提现方式选择
			typeChange(value){
				if(value){
					this.initBankCardList(value);
				}
			},
			
			// 全部提现
			allWithdrawal(){
				this.withdrawalForm.amount = this.allAmount;
			},
			
			// 获取银行卡列表
			initBankCardList(type){
				this.$api.accountList({
					token:uni.getStorageSync('user_token'),
					type:type,
				}).then(res=>{
					if(res.code == 0){
						this.accountOptions = res.data.map((item,index)=>{
							return {
								text: item.code_num + '（' + item.name  + '）',
								value: item.id
							}
						});
					}else{
						uni.showToast({
							title: res.msg,
							icon: 'none'
						});
					}
				});
			},
			
			// 跳转提现记录
			goDetail(){
				uni.navigateTo({
					// 保留当前页面，跳转到应用内的某个页面,使用uni.navigateBack可以返回到原页面
					url: "/pages/my/purse/withdrawal/withdrawalDetail",
				})
			},
			
			// 提现申请
			apply(){
				var plateform = 0;
				// #ifdef H5
				plateform = 1;
				// #endif
				
				// #ifdef MP-WEIXIN
				plateform = 2;
				// #endif
				this.$api.withdrawalApply({
					token:uni.getStorageSync('user_token'),
					deposit_money:this.withdrawalForm.amount,
					account_id:this.withdrawalForm.account,
					type:this.withdrawalForm.type,
					plateform:plateform,
				}).then(res=>{
					if(res.code == 0){
						uni.showToast({
							title: '提现成功',
							icon: 'success'
						});
						this.withdrawalForm = {
							type:"",
							account:"",
							amount:"",
						};
						this.getBalance();
						setTimeout(()=>{
							uni.navigateBack({
								delta: 1
							})
						})
					}else{
						uni.showToast({
							title: res.msg,
							icon: 'none'
						});
					}
				});
			},
		}
	}
</script>

